import React, { Component } from "react";
import "./HeaderComponent.css";
import { Row, Col, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { Login } from "../../../redux/actions/checkAuthorizeAction";
import { Route, Redirect, withRouter } from 'react-router-dom';

const orangeColorText = {
  color: "#f57224"
};
const whiteColorText = {
  color: "#fff"
};
const greenColorText = {
  color: "#30c8dc"
};


class HeaderComponent extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      currentPathName : ""
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.location !== prevProps.location) {
      this.setState({currentPathName : this.props.location.pathname}) ;
    }
  }

  _dispatchReduxLogOut(){
    const { history, authed } = this.props;
    this.props.dispatchReduxLogOut(false);
    localStorage.removeItem("token");
    localStorage.removeItem("typeUser");
    history.push("/");
  }

  componentDidMount(){
    console.log(this.props.location.pathname)
    this.setState({currentPathName : this.props.location.pathname}) ;
  }
  render() {
    return (
      <div className="Header-app" id="Header">
        <div className="Reset-margin Header-app">
          <div className="Full-width Header-background-first " id="Header-menu">
            <Row className="Center Header-contact">
              <div className="Container-custom">
                <span>Cho mng bn  n vi Bee Go !</span>
                <Button className={"Logout-button"}  onClick={this._dispatchReduxLogOut.bind(this)}> Logout !</Button>
                <a>
                  <i className="fab fa-twitter Icon-link" />
                </a>
                <a>
                  <i className="fab fa-facebook-f Icon-link" />
                </a>
                <a>
                  <i className="fab fa-google Icon-link" />
                </a>
              </div>
            </Row>
          </div>

          <div
            className="Full-width Header-background-Second"
            id="Search-header"
          >
            <Row className="Max-width Full-width">
              <Col xs={12} sm={6} md={3} lg={3} className="No-padding">
                <Link to="/" >
                  <img
                    alt="logo"
                    className="Logo-Beego"
                    src={require("../../../assets/image/beego.png")}
                  />
                </Link>
              </Col>
              <Col xs={12} sm={6} md={6} lg={6} className="No-padding">
                <div className="Feature-tag-search">
                  <a>Xu hng tm kim</a>
                  <a>Thi tramg</a>
                  <a>in t</a>
                </div>
                <div className="Div-search">
                  <input
                    className="Height-control Input-search"
                    placeholder="Tm kim trn BeeGo"
                    type="text"
                    name="name"
                  />
                  <Button className="Height-control Button-search">
                    Tm kim
                  </Button>
                  <span className="Arrow-left-search" />
                </div>
              </Col>
              <Col xs={12} sm={6} md={2} lg={2}  className={ "No-padding Account "  + (this.props.listState.isLogin === true ? "Display-none" : "")} >
                <ul className="Account-info">
                  <li>
                    <Link to="/login">ng nhp /</Link>
                  </li>
                  <li>
                    <Link to="/signup">ng k</Link>
                  </li>
                </ul>
              </Col>
              <Col xs={12} sm={6} md={1} lg={1} className="No-padding Cart">
                <Link to="/list-product-of-user"  className="Top-cart-contain heading-cart">
                  <span className="cartCount count_item_pr" id="cart-total">
                    0
                  </span>
                  <span className="cart-text">Gi hng</span>
                </Link>
              </Col>
              <Col xs={12} sm={6} md={2} lg={2}  className={ "No-padding Account "  + (this.props.listState.isLogin !== true ? "Display-none" : "")} >
                <Link to="/manage" className="Account-info">
                  <li> Trang c nhn  </li>
                </Link>
              </Col>
            </Row>

            <div className="Full-width Menu-cover">
              <div className="Menu-inside-border-dotted"> 
                <Row className="Row-menu" id = "Menu-detail">
                    <Col xs={12} sm={6} md={3} lg={2}  className="No-padding Border-left-menu Border-right-menu" >
                        <Link to="/manage/user-account"  style={{ color : this.state.currentPathName === "/manage/user-account" ||  this.state.currentPathName === "/manage"  ? "#fff" : "" }} className={"Child-Link1"} >Qun l ngi dng</Link>
                    </Col>
                    <Col xs={12} sm={6} md={3} lg={2}  className="No-padding Border-right-menu" >
                        <Link to="/manage/category"  style={{ color : this.state.currentPathName === "/manage/category" ? "#fff" : "" }} className="Child-Link1">Qun l danh mc</Link>
                    </Col>
                    <Col xs={12} sm={6} md={3} lg={2}  className="No-padding Border-right-menu" >
                        <Link to="/manage/discount"  style={{ color : this.state.currentPathName === "/manage/discount" ? "#fff" : "" }} className="Child-Link1">Qun l m gim gi</Link>
                    </Col>
                    <Col xs={12} sm={6} md={3} lg={2}  className="No-padding Border-right-menu" >
                        <Link to="/manage/payment"  style={{ color : this.state.currentPathName === "/manage/payment" ? "#fff" : "" }} className="Child-Link1">Qun l phng thc thanh ton</Link>
                    </Col>
                </Row>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

// listState : tn t cho danh sch cc state m ly ra c t store
// state l danh sch tt c cc state => c th ly ring r => state.isLogin

const  HeaderWithRouter =  withRouter(HeaderComponent);

const mapStateToProps = (state) => {
  return {
    listState: state
  }
}

// isLogin == true =>  login
const mapDispatchToProps = dispatch => {
  return {
    dispatchReduxLogOut: (isLogin) => dispatch(Login(isLogin)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HeaderWithRouter);