// reducer chi tinh toan 
// Given the same arguments, it should calculate the next state and return it. No surprises. No side effects. No API calls. No mutations. Just a calculation.
// function chi tiet cua Action
// i chiu action.type bn file checkoAuthorizeAction.js => xc nhn s thc hin Action no

const checkAuthorizeReducer = (stateIsLogin = false , action) => {
    switch (action.type) {
      case 'LOGIN':
        return action.isLogin;// l gi tr m checkAuthorizeAction.js tr v (isLogin)
      default:
        return stateIsLogin;// gi tr mc nh khi khng c gi tr
     
    }
};
  
export default checkAuthorizeReducer;
  